<template>
    <ul class="blog--list" >
        <li class="blog--item"  v-for="post in responseData" :key="post.id">
            <router-link :to="{name: 'BlogView', params: { id: post.id }}">
                <figure class="blog--thumbnail">
                    <img :src="blogInfoData[0]" alt="thumbnail">
                </figure>
                <div class="blog--context">
                    <div class="blog--hashtag">
                        <em>{{ blogInfoData[2]}}</em>
                    </div>
                    <em class="blog--title">{{ post.title }}</em>
                    <!-- <div class="blog--text">
                        엘루오의 DT(Digital Transformation) 깊게 들여다 보기 10<br />
                        Frog Design의 Adaptive Design, IDEO의 'Design Thinking' 방법론
                    </div> -->
                    <div class="blog--info">
                        <span class="blog--date">{{ formatDate(post.date) }}</span> 
                        <span class="blog--writer">{{ blogInfoData[1] }}</span>
                    </div>
                </div>
            </router-link>
        </li> 
    </ul>
</template>
      
<script>
import { ref, onMounted, toRefs, reactive } from 'vue';
import { gsap, Power2 } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
gsap.registerPlugin(ScrollTrigger);

export default {
    name: 'Work',
    props: {
        responseData: {
            type: Array,
            required: true
        },
        blogInfoData: {
            type: Array,
            required: true
        }
    },
    setup(props) {
        // console.log( props );


        const formatDate = (dateString) => {
            const dateObject = new Date(dateString);
            const year = dateObject.getFullYear();
            const month = String(dateObject.getMonth() + 1).padStart(2, '0');
            const day = String(dateObject.getDate()).padStart(2, '0');
            return `${year}-${month}-${day}`;
        }
        onMounted(() => {
        });
        

        return {
            formatDate
        };
    }
}
</script>
      
<style lang="scss" scoped>
@import "@/assets/scss/__variables.scss";

.blog {
    &--list {
        padding:0 200px;
        margin-block: 100px;
        >li {
           border-top: 2px solid #181818;
           >a{
                display: flex;
                justify-content: space-between;
                align-items: center;
                &:hover{
                    .blog--title{
                        &:before{
                            content: '';
                        }
                    }
                }
            }
            &:last-child{
                border-bottom: 2px solid #181818;
            }   
        }
    }
    &--thumbnail{
        width:40%;
        display: flex;
        justify-content: center;
        align-content: center;
        padding:60px 0;
        >img{
            
        }
    }
    &--context{ width: 60%; }
    &--hashtag{ 
        display: -webkit-box;
        overflow: hidden;
        text-overflow: ellipsis;
        max-height: 100px;
        min-height: 29px;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        margin-bottom: 20px;
        >em{
            display: block;
            width: 100%;
            font-family: 'Noto Sans Korean';
            color: $dpColor;
            font-size: 25px;
            font-weight: 600;  
            letter-spacing: -0.5px;
        }
    }
    &--title{
        display: -webkit-box;
        overflow: hidden;
        text-overflow: ellipsis;
        max-height: 85px;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        font-size: 40px;
        font-weight: bold;
        color: #222;
        letter-spacing: -1px;
        font-family: 'Noto Sans Korean';
        margin-bottom: 20px;
        &:before{
            // content: '';
            display: inline-block;
            width: 10px;
            height: 10px;
            border-radius: 20px;
            background: $dpColor;
            vertical-align: top;
            margin-top: 16px;
            margin-right: 10px;
           
        }
    }
    &--text{
        font-family: 'Noto Sans Korean';
        display: -webkit-box;
        overflow: hidden;
        text-overflow: ellipsis;
        max-height: 120px;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        font-size: 26px;
        line-height: 1.3;
        color: #555;
        letter-spacing: -1px;
        margin-bottom: 20px;
    }
    &--info{
        span{ font-size:20px; color: #888;}
    }
    &--date{ 
        display: inline-block;
        padding-right: 20px;
    }
    &--writer{
        display: inline-block;
        vertical-align: top;
        &::before{
            content: '';
            display: inline-block;
            width:14px;
            height: 14px;
            background:url("@/assets/images/icon_editor.png") 0 0 no-repeat;
            background-size:14px auto;
            padding-right: 6px;
        }
    }
   
}


</style>
      